.send-message {
  display: flex;
  flex-direction: row;
  width: 90%;
  margin: 10px auto;
 
  .message-input {
    width: 80%;
    height: 8vh;
    background: #f4a460;
    padding: 10px;
    color: #000000;
    font-family: 'Press Start 2P', cursive;   
    font-size: 2vh;
    border: none;
    resize: none;
  }
  .message-input-submit {
    width: 20%;
    font-family: 'Press Start 2P', cursive;
    font-size: 2.5vh;
    background-color: #e0412a;
    border: 2px solid #000000;
    padding: 4px;
    color: #f4a460;
    text-transform: uppercase;
    cursor: pointer;
    outline: none;
    &:hover {
      color: #564479;
      background: #f4a460;
    }
    &:active {
      transform: scale(0.9);
    }
  }
}
