.listMesseges {
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
  background: #f4a460;
  list-style-type: none;
  .message {
    background: #f4a460;
    .text-message {
      font-size: 2vh;
      .author-message {
        color: #f53232;
        font-size: 2.5vh;
        text-transform: uppercase;
      }      
      .time-message {
       color: #564479; 
       font-size: 1.5vh;
      }
    }
  }
}
